import React from 'react';
import { IconBase } from 'react-icons';

// Use custom component instead '@mssgme/icons/PlusIcon' because bundling messes icon node structure,
// making it harder to animate
export function PlusIcon(props) {
    return (
        <IconBase
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
            fill="none"
            viewBox="0 0 24 24"
            {...props}
        >
            <line x1="12" y1="5" x2="12" y2="19" />
            <line x1="5" y1="12" x2="19" y2="12" />
        </IconBase>
    );
}
