import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { AnimatePresence, motion } from 'framer-motion';

import { Theme } from '@mssgme/ui';
import { PlusIcon } from './PlusIcon';

import styles from './Accordion.scss';

const AccordionItem = ({ theme, item, isOpen, onToggle }) => {
    const { headerStyle, contentStyle } = useMemo(
        () => ({
            headerStyle: Theme.compile({
                ...theme,
                textStyle: theme.headerStyle,
            }),
            contentStyle: Theme.compile(theme),
        }),
        [theme]
    );
    const handleToggle = useCallback(() => void (onToggle && onToggle(item)), [item, onToggle]);

    return (
        <div className={styles.item}>
            <motion.div className={styles.title} initial={false} onClick={handleToggle} style={headerStyle}>
                <div className={styles.titleText}>{item.title}</div>
                <PlusIcon className={cn({ [styles.titleIcon]: true, [styles.opened]: isOpen })} size={20} />
            </motion.div>

            <AnimatePresence initial={false}>
                {isOpen && (
                    <motion.section
                        key="content"
                        initial="collapsed"
                        animate="open"
                        exit="collapsed"
                        variants={{
                            open: { opacity: 1, height: 'auto', marginTop: 8 },
                            collapsed: { opacity: 0, height: 0, marginTop: 0, overflow: 'hidden' },
                        }}
                        transition={{ duration: 0.6, ease: 'easeInOut' }}
                        className={styles.content}
                        style={contentStyle}
                    >
                        {item.content}
                    </motion.section>
                )}
            </AnimatePresence>
        </div>
    );
};

AccordionItem.propTypes = {
    item: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
    isOpen: PropTypes.bool,
    onToggle: PropTypes.func,
};

export default AccordionItem;
